let app = require("@/config");
let enums = require("../enumsv1.js");
export default {
	onShow() {
		this.getData();
	},
	onLoad() {},
	methods: {
		getData() {
			uni.post("/api/workflow/MyAttend", {name:"按团队申请实验室"}, msg => {
				this.roomData = msg.data;
			})
			uni.post("/api/workflow/MyAttend", {name:"按机位申请实验室"}, msg => {
				this.seatData = msg.data;
			})
		},
		toExecute(item) {
			item.StepId = undefined;
			if (item.WorkflowName === "按团队申请实验室") {
				uni.setStorage({
					key: 'jmpInfo',
					data: item,
					success: () => { //如果缓存成功则跳转
						uni.navigateTo({
							url: './roomFlowsCtrl'
						})
					}
				})
			} else if (item.WorkflowName === "按机位申请实验室") {
				uni.setStorage({
					key: 'jmpInfo',
					data: item,
					success: () => { //如果缓存成功则跳转
						uni.navigateTo({
							url: './seatFlowsCtrl'
						})
					}
				})
			}
		},
		/*// ListTouch触摸开始
		ListTouchStart(e) {
			this.listTouchStart = e.touches[0].pageX
		},

		// ListTouch计算方向
		listTouchMove(e) {
			this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
		},

		// ListTouch计算滚动
		listTouchEnd(e) {
			if (this.listTouchDirection == 'left') {
				this.modalName = e.currentTarget.dataset.target
			} else {
				this.modalName = null
			}
			this.listTouchDirection = null
		},*/
		foldUp(e) {
			this[e] = !this[e];
		}
	},
	data() {
		return {
			workflow: enums.workflow,
			wColor: enums.workflowColor,
			icon: app.webInfo.avatar,
			page: 1,
			pageSize: 10,
			roomData: [],
			seatData: [],
			modalName: null,
			listTouchStart: 0,
			listTouchDirection: null,
			displayRoom: true,
			displaySeat: true
		}
	}
}
